import React, { useState, useEffect } from "react";
import h1 from "./Images/bg-hero-1-1792-x1.jfif";
import h2 from "./Images/bg-hero-2-1792-x1.jfif";
import h3 from "./Images/bg-hero-3-1792-x1.jfif";
import h4 from "./Images/bg-hero-4-1792-x1.jfif";
import h5 from "./Images/bg-hero-5-1792-x1.jfif";
import $ from 'jquery';
import './HomeBanner.css'

const HomeBanner = () => {
  const [backImage, setbackImage] = useState(h1);
  const images = [h1, h2, h3, h4, h5];

  useEffect(() => {
    localStorage.setItem("x", 0);
    setInterval(function() {
      setbackImage(images[parseInt(localStorage.getItem("x"))]);
      localStorage.setItem("x", parseInt(localStorage.getItem("x")) + 1);

      if (localStorage.getItem("x") === 5) {
        localStorage.setItem("x", 0);
      }

$(".backimages").fadeIn('slow');

    }, 2000);
  }, []);

  return (
    <div>
      <section
       
        className="tr-banner section-before bg-image"
      >
        <div className="container">
          <div className="backimages"  style={{ backgroundImage: "url(" + backImage + ")" ,transition: "opacity 2s ease-in-out"}}></div>
          <div className="banner-content text-center">
            <h2>Find the best Freelancers in their finest hour</h2>
            <h3>Getting a job done has never been easy.</h3>

            <form className="form-horizontal">
              <div className="col-md-10 no-padd">
                <div className="input-group">
                  <input
                    type="text"
                    placeholder="What do you need to get done?"
                    className="form-control"
                  />
                </div>
              </div>
              <div className="col-md-2 no-padd">
                <div className="input-group">
                  <button
                    type="submit"
                    className="kafe-btn kafe-btn-mint full-width"
                  >
                    Let's Go!
                  </button>
                </div>
              </div>
            </form>

            <div className="row hidden-xs">
              <div className="col-lg-4 col-sm-6">
                <div className="features">
                  <span className="fa-stack fa-3x">
                    <i className="fa fa-circle fa-stack-2x"></i>
                    <i className="fa fa-clone fa-stack-1x fa-inverse"></i>
                  </span>
                  <p>Post your job for Free</p>
                </div>
              </div>

              <div className="col-lg-4 col-sm-6">
                <div className="features">
                  <span className="fa-stack fa-3x">
                    <i className="fa fa-circle fa-stack-2x"></i>
                    <i className="fa fa-list-alt fa-stack-1x fa-inverse"></i>
                  </span>
                  <p>Get proposals in minutes</p>
                </div>
              </div>
              <div className="col-lg-4 col-sm-6">
                <div className="features">
                  <span className="fa-stack fa-3x">
                    <i className="fa fa-circle fa-stack-2x"></i>
                    <i className="fa fa-users fa-stack-1x fa-inverse"></i>
                  </span>
                  <p>Choose your freelancer</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default HomeBanner;
