import * as atypes from "./types";
import axios from '../../axios-server';
import { setAlert } from "./alert";
import setAuthToken from "../../utills/setAuthToken";

const baseUrl="http://localhost:5000";
export const logout = () => dispatch => {

  localStorage.removeItem("userToken")
  dispatch({
    type: atypes.LOGOUT
  });
  
};
export const startLoading = () => dispatch => {
  dispatch({
    type: atypes.START_LOADING
  });
};
export const loginUser = (email, password) => async dispatch => {
  await axios
    .post(baseUrl+"/api/auth", { email, password },{
    headers: {
      'Content-Type': 'application/json'
    }
  })
    .then(response => {

    
      if(response.data.found){
        dispatch({
          type: atypes.LOGIN_SUCCESS,
          payload: response.data.token
        });


        localStorage.setItem("userToken",  response.data.token);
        dispatch(setAlert("Login successfully", "success"));
        dispatch(loadUser());
      }else{

        dispatch({
          type: atypes.LOGIN_ERROR
        });
        dispatch(setAlert("Invalid Credentials", "danger"));
       
      }
    
     
    })
    .catch(err => {
      console.log(err);
      // const error = err.response.data.error;
      dispatch({
        type: atypes.LOGIN_ERROR
      });

      // if (error) {
      //   error.forEach(e => dispatch(setAlert(e.msg, "danger")));
      // }
    });
};

export const loadUser = () => async dispatch => {

  await axios
    .get(baseUrl+"/api/users/userdata")
    .then(response => {

      dispatch({
        type: atypes.LOAD_USER,
        payload: response.data
      });

      console.log(response.data);
    })
    .catch(err => {
      dispatch({
        type: atypes.AUTH_ERROR
      });
    });
};

export const isEmailRegisterd = (email) => async dispatch => {

  await axios
    .get("/api/auth/checkemail")
    .then(response => {
     
      dispatch({
        type: atypes.LOAD_USER,
        payload: response.data
      });

      console.log(response.data);
    })
    .catch(err => {
      dispatch({
        type: atypes.AUTH_ERROR
      });
    });
};

export const registerUser = ({fname,lname, email, password,username}) => async dispatch => {
  const config = {
    headers: {
      "Content-Type": "application/json"
    }
  };

  const body = JSON.stringify({fname,lname, email, password,username });
  // try {
  await axios
    .post("http://localhost:5000/api/users", body, config)
    .then(response => {
      dispatch({
        type: atypes.REGISTER_SUCCESS,
        payload: response.data
      });

      dispatch(setAlert("Register successfully", "danger"));
    })
    .catch(err => {
      const errors = err.response.data.error;
      // console.log(err.response.data.error);

      if (errors) {
        errors.forEach(error => dispatch(setAlert(error.msg, "danger")));
      }
      dispatch({
        type: atypes.REGISTER_FAIL
      });
    });
};
export const deleteAccount = () => async dispatch => {
  if (window.confirm("Are you sure? This can not NOT be undone!")) {
    await axios
      .delete("/api/profile/")
      .then(response => {
        console.log(response.data);
        dispatch({ type: atypes.CLEAR_PROFILE });
        dispatch({ type: atypes.ACCOUNT_DELETED });

        dispatch(
          setAlert("Your account has been permanently deleted ", "success")
        );
      })
      .catch(err => {
        const error = err.response.data.error;
        if (error) {
          error.forEach(e => dispatch(setAlert(e.msg, "danger")));
        }

        dispatch({
          type: atypes.PROFILE_ERROR,
          payload: { msg: err.response.statusText, status: err.response.status }
        });
      });
  }
};
